@import url(https://fonts.googleapis.com/css?family=Lato:300,400,900|Playfair+Display:400,900i,900);
body {
  --black: hsl(0, 0%, 10%);
  --white: hsl(0, 0%, 100%);
  --grey:  hsl(0, 0%, 90%);
  --green: hsl(80, 44%, 70%);
  --peach: hsl(20, 80%, 80%);
  --pink:  hsl(360, 80%, 80%);
  --peach-gradient:
    radial-gradient(circle at 50% 0, hsla(20, 80%, 80%, 0.5), hsla(20, 80%, 80%, 0.7)),
    radial-gradient(circle at 6.7% 75%, hsla(340, 80%, 75%, 0.5), hsla(340, 80%, 75%, 0.7)),
    radial-gradient(circle at 93.3% 75%, hsla(280, 80%, 50%, 0.5), hsla(80, 80%, 50%, 0.7));

  color: var(--black);
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Playfair Display";
  font-weight: 900;
  margin: 0;
}

h1 {
  font-size: 3.5rem;
}

h4 {
  font-style: italic;
}

h5 {
  font-weight: 400;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Contact {
  -webkit-align-items: center;
          align-items: center;
  background-color: var(--green);
  border-radius: 0.2rem;
  box-shadow: 0px 2px 4px 0px hsla(0,0%,0%,0.2);
  box-sizing: border-box;
  color: white;
  display: -webkit-inline-flex;
  display: inline-flex;
  padding: 0.3em 0.5em;
  text-decoration: none;
}

.Contact > :first-child {
  margin-right: 0.5em;
}

.Card {
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  width: calc(var(--app-width) / 2);
}

.Card > img {
  width: 100px;
  border-radius: 50%;
}

.Card > h1 {
  text-align: center;
  margin: 0;
}

.Card-contact {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}

.Card-contact > *:not(:last-child) {
  margin-right: 0.5em;
}

@media screen and (max-width: 900px) {
  .Card {
    width: 100vw;
  }
}

.Details {
  width: 100%;
}

.Details-header {
  border-bottom: solid 3px hsla(0, 0%, 100%, 0.3);
  border-top: solid 3px hsla(0, 0%, 100%, 0.3);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0.5em 0;
}

.Details-bio {
  padding-top: 1.25rem;
}

@media screen and (max-width: 540px) {
  .Details-header {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}

.Banner {
  background:
    -webkit-radial-gradient(50% 0, circle, hsla(20, 80%, 80%, 0.5), hsla(20, 80%, 80%, 0.7)),
    -webkit-radial-gradient(6.7% 75%, circle, hsla(340, 80%, 75%, 0.5), hsla(340, 80%, 75%, 0.7)),
    -webkit-radial-gradient(93.3% 75%, circle, hsla(280, 80%, 50%, 0.5), hsla(80, 80%, 50%, 0.7));
  background:
    radial-gradient(circle at 50% 0, hsla(20, 80%, 80%, 0.5), hsla(20, 80%, 80%, 0.7)),
    radial-gradient(circle at 6.7% 75%, hsla(340, 80%, 75%, 0.5), hsla(340, 80%, 75%, 0.7)),
    radial-gradient(circle at 93.3% 75%, hsla(280, 80%, 50%, 0.5), hsla(80, 80%, 50%, 0.7));
  box-sizing: border-box;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 2em 0;
}

.Banner-content {
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  grid-area: content;
  width: var(--app-width);
}

.Banner-content > * {
  box-sizing: border-box;
  padding: 1em 0 1em 1em;
}

@media screen and (max-width: 900px) {
  .Banner-content {
    -webkit-align-items: center;
            align-items: center;
    box-sizing: border-box;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 0 1em;
  }

  .Banner-content > * {
    padding: 1em;
  }
}

ul.List {
  margin: 0;
  padding: 1em 0 1em 2em;
}

ul.List li {
  margin-bottom: 0.5em;
}

ul.List li p {
  margin: 0;
}

section.Section {
  padding: 1em 0;
}

section.Section h3 {
  color: var(--pink);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

section.Section h3::before, section.Section h3::after {
  background: var(--peach-gradient);
  content: '';
  display: inline-block;
  width: 100%;
  height: 3px;
}

section.Section h3::before {
  margin-bottom: 0.5em;
}

section.Section h3::after {
  margin-top: 0.5em;
}

section.Section .content {
  padding: 1em 0;
}

.Education {

}

.Education .school {
  display: grid;
  grid-template-areas:
    "name daterange"
    "degree degree"
    "achievements achievements";
  grid-template-columns: 1fr 1fr;
  grid-template-rows: -webkit-min-content -webkit-min-content -webkit-min-content;
  grid-template-rows: min-content min-content min-content;
}

.Education .school .name {
  grid-area: name;
}

.Education .school .daterange {
  grid-area: daterange;
  text-align: right;
}

.Education .school .degree {
  grid-area: degree;
}

.Education .school .achievements {
  grid-area: achievements;
}

.Experience .company {
  display: grid;
  grid-template-areas:
    "name daterange"
    "title title"
    "duties duties"
    "achievements achievements";
  grid-template-columns: 1fr 1fr;
  grid-template-rows: -webkit-min-content -webkit-min-content -webkit-min-content -webkit-min-content;
  grid-template-rows: min-content min-content min-content min-content;
}

.Experience .company .name {
  grid-area: name;
}

.Experience .company .title {
  grid-area: title;
}

.Experience .company:not(:first-child) .name, .Experience .company:not(:first-child) .daterange {
  margin: 1em 0 0 0;
}

.Experience .company .title {
  margin: 0 0 1em 0;
}

.Experience .company .daterange {
  grid-area: daterange;
  text-align: right;
}

.Experience .company .duties {
  grid-area: duties;
  margin: 0;
}

.Experience .company .achievements {
  grid-area: achievements;
}

@media screen and (max-width: 600px) {
  .Experience .company {
    grid-template-areas:
      "name"
      "title"
      "daterange"
      "duties"
      "achievements";
    grid-template-columns: 1fr;
    grid-template-rows: -webkit-min-content;
    grid-template-rows: min-content;
  }

  .Experience .company:not(:first-child) .daterange, .Experience .company .title {
    margin: 0;
  }

  .Experience .company:not(:first-child) .daterange, .Experience .company .daterange {
    margin: 0 0 1em 0;
    text-align: left;
  }
}

.Skills {
  -webkit-columns: auto 3;
          columns: auto 3;
  width: 100%;
}

.Skills .Skill {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 0.5em;
}

.Skills .Skill .name {
  -webkit-flex: 1 1;
          flex: 1 1;
  color: var(--black);
}

.Skills .Skill .ranking {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.Skills .Skill .ranking > * {
  --circ: 0.75em;
  border-radius: 50%;
  border-width: 2px;
  border-style: solid;
  height: var(--circ);
  margin-left: 0.25em;
  width: var(--circ);
}

.Skills .Skill .ranking.fresh > * {
  border-color: var(--green);
}

.Skills .Skill .ranking.fresh > .level.yes {
  background-color: var(--green);
}

.Skills .Skill .ranking.stale > .level.yes {
  background-color: var(--grey);
}

.Skills .Skill .ranking.stale > * {
  border-color: var(--grey);
}

@media screen and (max-width: 900px) {
  .Skills {
    -webkit-columns: auto 2;
            columns: auto 2;
  }
}

@media screen and (max-width: 450px) {
  .Skills {
    -webkit-columns: auto 1;
            columns: auto 1;
  }
}

.Resume {
  box-sizing: border-box;
}

@media screen and (max-width: 900px) {
  .Resume {
    padding: 0 1em;
  }
}

.App {
  --app-width: 900px;
  display: grid;
  grid-template-areas:
    "banner banner banner"
    "leftGutter resume rightGutter";
  grid-template-columns: 1fr -webkit-min-content 1fr;
  grid-template-columns: 1fr min-content 1fr;
  grid-template-rows: -webkit-min-content 1fr;
  grid-template-rows: min-content 1fr;
  height: 100vh;
}

.App-banner {
  grid-area: banner;
  width: 100vw;
}

.App-resume {
  grid-area: resume;
  width: var(--app-width);
}

@media screen and (max-width: 900px) {
  .App-resume {
    width: 100vw;
  }
}

